$color-text: #15191E;
$color-text2: #65758B;
$color-text3: #A7B1BF;



$color-white: #ffffff;
$color-white2: #F7F6F5;
$color-white3: #E8E6E1;





$color-black: #000000;
$color-black2: #1F1F1F;


$color-gray: #696660;
$color-gray2: #C2C9D3;
$color-gray3: #403E3A;
$color-gray4: #F5F5F5;
$color-gray5: #8C8982;
$color-gray6: #595959;
$color-gray7: #F2F0ED;
$color-gray8: #E5E7EB;
$color-gray9: #6B7280;



$color-linear: linear-gradient(143.23deg, #F26428 48.77%, #FFFF6D 91.27%);




$color-ogr: #FCB247;
$color-red: #FF1010;
$color-green: #009033;
$color-green2: #10B981;



@mixin max($value) {
  @media screen and (max-width: $value) {
    @content;
  }
}